<template>
  <div class="navigation">
    <div class="container">
      <div class="navigation__content">
        <router-link
          aria-label=""
          class="logo"
          title="main-page"
          :to="{ name: 'home', params: { lang: $route.params.lang } }"
          @click="goToTop()"
        >
          <img src="@/assets/logo.png" alt="logo" />
        </router-link>
        <div class="navigation__content_item" :class="{ show: mobileMenu }">
          <button
            v-for="(item, index) in NavLinks"
            :key="index"
            class="navigation__content_item-btn"
            :id="item.id"
            @click="scrollToSection(item.id)"
          >
            {{ item.link[activeLanguage] }}
          </button>
        </div>
        <div class="navigation-list-mobile" :class="{ active: menuOpen }">
          <div class="navigation-list-mobile-list">
            <button
              class="navigation-list-btn"
              v-for="(item, index) in NavLinks"
              :key="index"
              :id="item.id"
              @click="scrollToSection(item.id)"
            >
              {{ item.link[activeLanguage] }}
            </button>
          </div>
          <a
            href="tel: +998 90 980 53 00"
            class="navigation-list-mobile-list-link"
            ><font-awesome-icon
              class="navigation-list-mobile-list-link-icon"
              icon="fa-solid fa-phone"
            />
            &nbsp; +998 90 980 53 00
          </a>
        </div>
        <button
          class="menu-burger"
          :class="{ close: menuOpen }"
          @click="openMenu"
        >
          <span class="menu-burger-line"></span>
          <span class="menu-burger-line"></span>
          <span class="menu-burger-line"></span>
        </button>
        <div class="navigation__language">
          <ul class="navigation__content-num">
            <!-- <p class="navigation__language-current">{{ activeLanguage}}</p> -->
            <li
              class="navigation__content-language"
              v-if="activeLanguage != 'ru'"
            >
              <button class="ru" @click="changeLanguage('ru')">RUS</button>
            </li>
            <li
              class="navigation__content-language"
              v-if="activeLanguage != 'en'"
            >
              <button class="en" @click="changeLanguage('en')">ENG</button>
            </li>
            <li
              class="navigation__content-language"
              v-if="activeLanguage != 'uz'"
            >
              <button class="uz" @click="changeLanguage('uz')">UZB</button>
            </li>
          </ul>
          <font-awesome-icon
            class="arrow-down"
            icon="fa-sharp fa-solid fa-chevron-down"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mobileMenu: false,
      opened: false,
      mobileCourses: false,
      dropDownOpened: false,
      viewportWidth: window.innerWidth,
      menuOpen: false,
      choose: false,
      NavLinks: [
        {
          link: { ru: "Наши услуги", uz: `Xizmatlar`, en: "Our services" },
          id: "#services",
        },
        {
          link: { ru: "O нас", uz: `biz haqimizda`, en: "About us" },
          id: "#about-us",
        },
        {
          link: {
            ru: "Нам доверяют",
            uz: `bizga ishonishadi`,
            en: "We are trust",
          },
          id: "#trust",
        },
        {
          link: { ru: "Контакты", uz: `Aloqa`, en: "Contacts" },
          id: "#contacts",
        },
      ],
    };
  },
  components: {},
  methods: {
    // openLang() {
    //   this.choose = !this.choose;
    // },
    openMenu() {
      this.menuOpen = !this.menuOpen;
    },
    goToTop() {
      window.scrollTo(0, 0);
    },
    changeLanguage(language) {
      const lang = language == "ru" ? "" : language;
      this.$router.push({
        name: this.$route.name,
        params: { lang: lang },
      });
      const newLang = language == "ru" || language == "" ? "ru" : language;
      this.$store.dispatch("selectLanguage", newLang);
    },
    scrollTo(sectionId) {
      if (this.menuOpen) {
        this.menuOpen = false;
      }
      const section = document.querySelector(sectionId);
      const scrolDistance = section.offsetTop - 50;
      let distance = window.scrollY;
      const scroll = () => {
        if (scrolDistance > distance + 0.5 || scrolDistance < distance - 0.5) {
          distance -= (distance - scrolDistance) / 15;
          window.scrollTo(0, distance);
          this.timer = setTimeout(() => {
            scroll();
          }, 5);
        } else {
          window.scrollTo(0, scrolDistance);
          stop();
        }
      };
      const stop = () => {
        clearTimeout(this.timer);
        window.removeEventListener("wheel", stop);
      };
      stop();
      window.addEventListener("wheel", stop);
      scroll();
    },
    scrollToSection(sectionId) {
      if (this.$route.name != "home") {
        this.$router.push("/").then(() => {
          setTimeout(() => {
            this.scrollTo(sectionId);
          }, 300);
        });
      } else {
        this.scrollTo(sectionId);
      }
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
};
</script>

